//title in pages
/* index home page font*/
.title {
    font-size: 3rem;
    text-align: center;
  
    @include pad {
      font-size: 2rem;
      margin-bottom: 2rem;
    }
  
    @include mobile {
      font-size: 1.25rem;
  
      margin-bottom: 1.25rem;
    }
  }
  /* tab pages font */
  .tab__title {
    /* 64px */
    font-size: 3rem;
    line-height: 1.66;
    margin-bottom: 3.75rem;
  
    @include mobile {
      font-size: 1.75rem;
    }
  }
  
  .text-xs {
    font-size: 0.875rem; /* 14px */
  }
  
  .text-s {
    font-size: 1.25rem; /* 20px */
    @include pad {
      font-size: 1rem;
    }
  }
  
  .text-m {
    font-size: 1.5rem; /* 24px */
    @include pad {
      font-size: 1rem;
      line-height: 1.8;
    }
  }

  /* spacial font size product title */


.fz-42 {
  font-size: 2.625rem;
}