/* 門市清單排版 */
.store__lists {
  display: flex;
  flex-wrap: wrap;

  @include pad {
    justify-content: space-between;
  }
}

/* 門市資訊 RWD 卡片排版 */
.store__card {
  max-width: 31.5%;

  @include pad {
    max-width: 48%;
  }

  @include mobile {
    max-width: 100%;
  }
  &__img {
    width: 100%;
    height: 224px;
    object-fit: cover;
    object-position: center;

    border-top-right-radius: 2px;
    border-top-left-radius: 2px;

    @include pad {
      height: 164px;
    }
  }

  &__content {
    i {
      width: 24px;
      height: 24px;
    }
    p {
      display: inline;
    }
  }
}