/* header menu bar */
// components
.header {
  display: flex;
  justify-content: space-between;
  align-items: center;

  @include mobile {
    flex-direction: column;
    justify-content: flex-start;
  }
}


.menu {
  display: flex;
  justify-content: space-between;

  @include mobile {
    flex-wrap: wrap;
    width: 100%;

    /* 
    border: 1px solid $white; */
    border-bottom: 0;
  }

  li {
    @include mobile {
      width: 50%;
    }
  }

  a {
    display: block;
    &:hover {
      text-shadow: rgba(255, 255, 255, 0.1) -1.5px -1.5px 1px,
        rgba(0, 0, 0, 0.75) 1px 1px 1px;
      opacity: 1;
    }

    @include mobile {
      text-align: center;
      padding: 8px 54px;
    }
  }
}
