//article
.tags {
  &__aside {
    width: 22%;
    @include mobile {
      width: 100%;
    }
  }
  &__header {
    display: flex;

    img {
      width: 38px;

      @include pad {
        width: 19px;
      }
    }
    h4 {
      @include pad {
        line-height: 42px;
      }
    }
  }
  &__content {
    a {
      /* tags hover button component */
      &:hover {
        display: inline-block;
        background-color: $primary;
        color: $white;
        border-radius: 5px;
        opacity: 1;
      }

      @include pad {
        line-height: 2.25;
        /*  margin-bottom: -8px; */
      }
    }
  }
}
