/* 不顯示 */
.lg-no-display {
    display: none;
  }
  
  .md-no-display {
    @include pad {
      display: none;
    }
  }
  
  /* display */
  .lg-display {
    display: block;
  }
  
  .md-display {
    @include pad {
      display: block;
    }
  }
  /* 不顯示 */
  
  .sm-display {
    @include mobile {
      display: block;
    }
  }
  
  .sm-no-display {
    @include mobile {
      display: none;
    }
  }