/* product components */
/* product menu */
.product {
  &__list {
    width: 100%;
    display: flex;
  }

  &__menu {
    width: 33.3333%;

    /* 排版 */
    a {
      display: flex;
      justify-content: center;
      align-items: center;
      height: 96px;
      &:hover {
        border-bottom: 4px solid $primary;
        padding-bottom: -4px;
      }

      @include pad {
        height: 60px;
      }

      @include mobile {
        height: 30px;
      }
    }

    img {
      width: 44px;
      height: 44px;
      object-fit: contain;
      margin-right: 10px;
      @include pad {
        width: 30px;
        height: 30px;
      }
      @include mobile {
        width: 14px;
        height: 14px;
        margin-right: 2px;
      }
    }
  }
}
/* product banner */
.product__banner {
  display: flex;

  img {
    width: 50%;
    height: 304px;
    object-fit: cover;
    object-position: center;
    @include mobile {
      height: 148px;
    }
  }
}


/* product list */
.product {
  &__list {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
  }

  &__content {
    img {
      width: 100%;
      height: 164px;
      object-fit: cover;
      object-position: center;

      @include mobile {
        height: 166px;
      }
    }
  }

  &__price {
    display: flex;
    justify-content: space-between;
  }

  &__color {
    display: flex;
  }
}

/* color */
.color {
  &__choice {
    display: block;
    width: 24px;
    height: 24px;
    border-radius: 4px;

    @include mobile {
      width: 16px;
      height: 16px;
    }
  }
}

/*optical content li*/

.optical__content {
  width: 23%; /* optical */

  @include pad {
    width: 47.8%;
  }
  @include mobile {
    width: 100%;
  }
}

/* sungalsses content li */

.sunglasses__content {
  width: 31.5%;

  @include pad {
    width: 47.8%;
  }
  @include mobile {
    width: 100%;
  }
}
