
/* 文章cards */

.blog {
  &__body {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;

    @include mobile {
      flex-direction: column;
    }
  }

  &__img {
    max-width: 57%;
    height: auto;
    object-fit: contain;

    @include pad {
      max-width: 48%;
      height: auto;
      object-fit: cover;
    }

    @include mobile {
      max-width: 100%;
    }
  }

  &__content {
    max-width: 40%;

    @include pad {
      max-width: 46%;
    }

    @include mobile {
      max-width: 100%;
    }
    h3 {
      line-height: 1.9;
    }
  }

  &__subtitle {
    display: flex;
    justify-content: space-between;

    p {
      @include pad {
        line-height: 30px;
      }
    }
    span {
      opacity: 0.7;
      @include pad {
        line-height: 30px;
      }
    }
  }
  &__article {
    @include mobile {
      display: none;
    }
  }

  &__link__lg {
    display: block;

    @include pad {
      display: none;
    }
  }

  &__link__md {
    display: none;
    @include pad {
      display: inline;
    }
    @include mobile {
      display: none;
    }
  }
}
