//blog navbar 

.blog {
    display: flex;
    justify-content: space-between;
    align-items: center;
  
    &__menu {
      display: flex;
    }
    &__catagories {
      a {
        display: block;
        &:hover {
          background-color: $primary;
          border-radius: 5px;
          opacity: 1;
        }
      }
    }
  }
  
  .search {
    a {
      display: block;
    }
  
    img {
      width: 29px;
      height: 29px;
    }
  }
  