/* pagination */
.pagination {
  display: flex;
  justify-content: center;

  a {
    display: block;
    color: $gray-500;
    background-color: $white;
    border: 1px solid $gray-500;
    font-size: 1.125rem;

    &:hover {
      background-color: $black;
      border: 1px solid $black;
      color: $white;
      opacity: 1;
    }

    @include mobile {
      font-size: 17px;
    }
  }

  &__turnto {
    padding: 12px 12px;
  }

  &__pages {
    padding: 12px 16px;
  }

  &__default {
    background-color: $secondary;
  }
}
