/* tags */

.article {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;

  &__tags {
    display: flex;

    &__link {
      display: block;

      /* tags hover button */
      &:hover {
        display: inline-block;
        background-color: $primary;
        color: $white;
        border-radius: 5px;
        opacity: 1;
        margin-bottom: -8px;
      }
    }
  }

  &__body {
    width: 74%;

    @include mobile {
      width: 100%;
    }
  }

  &__subtitle {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  &__share {
    display: flex;

    @include mobile {
      margin-bottom: 10px;
    }
  }
  &__footer {
    @include pad {
      display: none;
    }
    @include mobile {
      display: block;
    }

    &__md {
      display: none;
      @include pad {
        display: block;
      }
      @include mobile {
        display: none;
      }
    }
  }
}

/* 文章照片 */
.post {
  max-width: 100%;
  height: 539px;
  object-fit: cover;
  object-position: center;

  @include pad {
    height: 292px;
  }

  @include mobile {
    height: 225px;
  }
}

.share {
  &__list {
    display: flex;
  }

  &__icon {
    display: block;
  }
}

//component
.article {
  &__pagination {
    display: flex;
    justify-content: space-between;

    @include mobile {
      flex-direction: column;
      justify-content: flex-start;
    }

    i {
      line-height: 30px;
    }
  }

  &__link {
    display: flex;
    line-height: 30px;
  }
}
